cut_copy_slice.addPlugin(
    'status',
    {
        init : function()
        {
            $('[href*="cut_copy_slice/status/status"][href*="status="]').each(function(i, btn){
                var setStatus = $(btn).attr('href').match(/status=(\d)/),
                    sliceContainer = $(btn).parents('.rex-slice-output');

                if(sliceContainer.length && setStatus)
                {
                    setStatus = parseInt(setStatus[1]) === 0;
                    if(setStatus)
                    {
                        sliceContainer.first().removeClass('cut_copy_slice--status--inactive');
                    }
                    else
                    {
                        sliceContainer.first().addClass('cut_copy_slice--status--inactive');
                    }
                }

                $(btn).on('click.cut_copy_slice', function(e){

                    $.pjax(
                    {
                        url: $(this).attr('href'),
                        container: '#rex-js-page-main-content',
                        fragment : '#rex-js-page-main-content',
                        push : false
                    });

                    return false;
                })
            });
        }
    }
);
